import React from 'react'
import styled from 'styled-components'
import chroma from 'chroma-js'
import { themeColors, colors } from '../../../constants'

const Container = styled.div`
  padding: 8px;
  font-size: 16px;
  background: ${({ active }) => active ? themeColors.primary : colors.white};
  color: ${({ active }) => active ? colors.white: colors.black};
  cursor: pointer;
  user-select: none;
  display: flex;
  align-items: center;
  &:hover {
    background: ${({ active }) => active ? chroma(themeColors.primary).brighten(0.1) : colors.gray300};
  }
  &:active {
    background: ${({ active }) => active ? chroma(themeColors.primary).brighten(0.3) : colors.gray200};
  }
`
const Image = styled.img`
  height: 20px;
  margin-right: 8px; 
`

const Item = ({ data, active, onClick }) => {
  const logoUrl = active ? data.logoLightUrl : data.logoUrl
  return (
    <Container onClick={onClick} active={active}>
      <Image src={logoUrl} />
      {data.name}
    </Container>
  )
}

export default Item
